import message from "./AxiosCreate"
async function getProductsById(id){
    const response =
    await message.Axios.get("/api/products/"+id
        ,
        {
            headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            token: localStorage.getItem("token")
            }
        }
        )
      console.log(response)
}
async function getProducts(){
    console.log(localStorage.getItem("token"))
    const response =
    await message.Axios.get("/api/products/"
        ,
        {
            headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            token: localStorage.getItem("token")
            }
        }
        )
      return response
}
async function createProducts(name,price,quantity){
    const response =
    await message.Axios.post("/api/products/"
        ,
        {
            "name":name,
            "price":price,
            "quantity":quantity
        }
        ,
        {
            headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            token: localStorage.getItem("token")
            }
        }
        )
      console.log(response)
}
async function updateProducts(id,name,price,quantity){
    const response =
    await message.Axios.put("/api/products/"+id
        ,
        {
            "name":name,
            "price":price,
            "quantity":quantity
        }
        ,
        {
            headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            token: localStorage.getItem("token")
            }
        }
        )
      console.log(response)
}
async function deleteProducts(id){
    const response =
    await message.Axios.delete("/api/products/"+id
        ,
        {
            headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
            token: localStorage.getItem("token")
            }
        }
        )
      console.log(response)
}
async function getItems(){
    return await message.fetchCsrf().then(()=>{return getProducts()})
}
async function getItemsById(id){
    message.fetchCsrf().then(()=>{getProductsById(id)})
}
async function addProducts(name,price,quantity){
    message.fetchCsrf().then(()=>{createProducts(name,price,quantity)})
}
async function changeProducts(id,name,price,quantity){
    message.fetchCsrf().then(()=>{updateProducts(id,name,price,quantity)})
}
async function removeProducts(id){
    message.fetchCsrf().then(()=>{deleteProducts(id)})
}
export const items = {getItems,getItemsById,addProducts,changeProducts,removeProducts,message}