        <img :src="require(`../assets/${item.products.image}`)" alt="Product Image" class="w-12 h-12 object-cover mr-4">
        <div>
          <h3 class="text-lg font-semibold">{{ item.products.name }}</h3>
          <p class="text-gray-500">{{ item.products.price }} €</p>
        </div>
      </div>
    </div>
    <div class="mt-8">
      <p class="text-lg font-semibold">Total: {{ totalAmount }} €</p>
    </div>
  </div>
</div>
</template>

<script>
import {panier} from "../services/panier"
export default {
  name: 'CartPage',
  data() {
    return {
      
      cartItems: [
        
      ],
    };
  },async mounted(){
    
    this.cartItems=(await panier.getAllPanier()).data
    console.log(this.cartItems)
  },
  computed: {
    imageBasePath() {
      return '../assets/'; // Base path for your images
    },
    totalAmount() {
      return this.cartItems.reduce((acc, item) => acc + item.products.price, 0);
    },
  },
};
</script>

