<template>
  <div class="min-h-screen flex flex-col items-center justify-center">
    <h1 class="text-3xl font-bold mb-8">QR Code Reader</h1>
    <div class="relative w-full max-w-md">
      <vue-web-cam ref="webcam" class="w-full h-auto" :height="480" :width="640" @cameras="onCameras" @capture="onCapture" />

      <!-- Add a semi-transparent overlay for style -->
      <div class="absolute inset-0 bg-black opacity-50"></div>
      <div class="absolute inset-0 flex items-center justify-center">
        <!-- Replace the text with a button to open the modal -->
        <button @click="openModal" class="text-white text-lg bg-slate-800 hover:bg-blue-700 px-4 py-2 rounded">Scan the QR code</button>
      </div>
    </div>

    <!-- Add a modal for camera access -->
   <!---
   <modal name="camera-modal">
      <div class="p-4">
        <p class="text-lg">Allow access to the camera</p>
        <button @click="requestCameraAccess" class="mt-4 bg-blue-500 hover:bg-blue-700 text-white px-4 py-2 rounded">Allow</button>
      </div>
    </modal>-->
  </div>
</template>

<script>
import { BrowserMultiFormatReader } from '@zxing/library';

export default {
  methods: {
    onCameras(cameras) {
      if (cameras && cameras.length > 0) {
        this.$refs.webcam.start(cameras[0]);
      } else {
        console.error('No cameras found');
      }
    },
    onCapture(result) {
      console.log('QR Code Result:', result);
      // Handle the QR code result as needed
      // For example, you can emit an event or perform a navigation
    },
    openModal() {
      // Open the camera access modal
      this.$modal.show('camera-modal');
    },
    requestCameraAccess() {
      // Add logic to request camera access here
      // You can use a library like https://developer.mozilla.org/en-US/docs/Web/API/MediaDevices/getUserMedia
      // After successful access, start the camera
      // Example: navigator.mediaDevices.getUserMedia({ video: true })
      //   .then(stream => {
      //     // Handle success
      //     this.$refs.webcam.start(stream);
      //   })
      //   .catch(error => {
      //     // Handle error
      //     console.error('Error accessing camera:', error);
      //   });
      // For simplicity, the example does not include the full implementation for camera access
    },
  },
  mounted() {
    const codeReader = new BrowserMultiFormatReader();
    codeReader.getVideoInputDevices()
      .then((cameras) => this.onCameras(cameras))
      .catch((err) => console.error(err));
  },
};
</script>

